/* Style pour le conteneur des compétences */
.skills-container {
    align-items: center; /* Centre les éléments verticalement */
    padding: 20px; /* Ajoute un espace intérieur de 20px */
    background-color: #fefefe; /* Couleur de fond blanche */
    border-radius: 1vw; /* Bords arrondis */
    overflow: hidden; /* Cache le débordement */
    width: 100%; /* Ajuste la largeur à 100% */
    white-space: nowrap; /* Empêche le retour à la ligne */
    position: relative; /* Position relative */
}

.skills {
    display: flex; /* Utilise le modèle de boîte flexible */
    flex-direction: column; /* Aligne les éléments en ligne */
    height: 15%;
    margin-top: 1%;
}

.h1b {
        font-family: 'Cursive', sans-serif;
        font-weight: bold;
        color: #d93b3b;
        margin-left: 2%;
}

/* Style pour les éléments de défilement horizontal */
.horizontal-scroll {
    display: flex; /* Utilise le modèle de boîte flexible */
    gap: 20px; /* Espace entre les éléments */
    width: 100%; /* Ajuste la largeur à 100% */
    animation: scroll 60s linear infinite; /* Animation de défilement */
}

/* Animation pour le défilement horizontal */
@keyframes scroll {
    0% {
        transform: translateX(0); /* Départ à gauche */
    }
    100% {
        transform: translateX(-50%); /* Défilement de moitié */
    }
}

/* Style pour les éléments de défilement horizontal */
.horizontal-scroll-item {
    height: 100px; /* Hauteur fixe */
    width: 150px; /* Largeur réduite */
    background-size: cover; /* Image de fond couvrante */
    background-position: center; /* Centre l'image de fond */
    display: inline-block; /* Affichage en ligne */
    transition: transform 0.4s ease-in-out; /* Transition fluide */
}

/* Effet au survol des éléments */
.horizontal-scroll-item:hover {
    transform: scale(1.1); /* Agrandit l'élément */
}

